<template>
  <div class="login">
    <form @submit.prevent="login">
      <input v-model="inputUsername" type="text" placeholder="Username"> <br>
      <input type="submit" placeholder="Join">
    </form>
  </div>
</template>

<script>
import ACTIONS from '@/store/actions.type'

export default {
  name: 'Login',
  data() {
    return {
      inputUsername: "",
    }
  },
  components: {
    
  },
  methods: {
    login() {
      this.$store.dispatch(ACTIONS.AUTH.LOGIN, {
        username: this.inputUsername,
      }).then(() => {
        this.$router.push({ name: 'Lobby' });
      })
    }
  }
}
</script>
